.logo {
  font-family: "Dr Sugiyama";
  margin: auto;
}
body{
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}
.pointer{
  cursor: pointer;
}
.profile-header {
 
  display: "inline-block";
  background-size: "cover";
  font-weight: "normal";
}

.profile-header-container{
  width: 100% !important;
  margin-left: 0 !important;
}

.profile-header-cover{
  height: 370px;
  background-size: cover;
  font-size: 4em;
  font-weight: normal;
  padding-top: 20px;
}
.profile-btn-controls{
  margin: auto;
  margin: 20px;

}

.profile-btn-controls-items{
  padding: 13px;
  box-shadow: 0 0 0 1px #2185d0 inset!important;
  border-radius: 30px;
  color: #000!important;
  font-size: 0.3em;
  background-color: aliceblue;
}
.btn{
  padding: 13px;

  border-radius: 30px;
}

.msg-container{
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
}

.edit-profile-form{
  padding-left: 40px;
  padding-right: 40px;
}
.profile-photo:hover {
opacity: 0.6;
cursor: pointer;
}
/* Header*/
.ui.secondary.pointing.menu{
  border-bottom : none;
}
.ui.secondary.pointing.menu .item{
  margin-top: auto;
  margin-bottom: auto;
  font-family: 'robotoregular';
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  height: 40px;
}
.ui.secondary.pointing.menu .active.item{
  border-bottom: none;
}
.redlocation{
  color: #FF0000 !important;
}
/*Header*/

/*home*/
.contenu{
  background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(https://thumbs.gfycat.com/WarmAllBedlingtonterrier-size_restricted.gif);
    background-attachment: fixed;
    background-blend-mode: luminosity;
    width: 100%;
    height: 100%;
    z-index: 0;
    position: fixed;
    filter: blur(3px)
}
.ui.card{
  width: 100%;
  display: inline-flex;
  padding: 0px 5px;
}
.red.point.small.icon,i.icon{
  width: unset;
}
.ui.card .meta{
  margin-left: 3em;
  margin-top: -0.9em;
  font-size: 11px;
}
.comment.icon.small{
  margin: 0px 10px;
}
.ellipsis.horizontal.large.icon{
  float : right;
  color : black;  
}
.heart{
  color: #000;
}
.unlock{
  color: blue;
}
.privacy{
  float: right;
  margin-top: auto;
  margin-bottom: auto;
}
.mycards{
  width: 50%;
  padding: 10px 10px;
  display: inline-block;
}
.textSlider{
  color: #fff;
  font-family: 'robotoregular';
  font-size: 35px;
  font-weight: bold;
  text-align: center;
  line-height: 1.45em;
  margin-top: 2.5em;
}
.previousButton,.nextButton{
  top: 60% !important;
}
.arrow.circle.icon{
  color: #fff;
}
a.nextButton{
  right :5px !important;
}
a.previousButton{
  left: 5px !important;
}
.signup,.signin{
  width: 85%;
  margin: 5px !important;
  border-radius: 2em !important;
}
.signUp{
  width: 100%;
  margin: 5px !important;
  border-radius: 2em !important;
}
.buttons{
  text-align: center;
}
.modal>.header>img{
  margin-left: auto;
  margin-right: auto;

}
.headerText{
  width: 100%;
  padding: 10px 20px;
  text-align: center;
}
.ui.form .field{
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}
.footerModal{
  background: #f9fafb;
  padding: 1rem 1rem;
  border-top: 1px solid rgba(34,36,38,.15);
  margin: 0px !important;
  width: 100% !important;
  text-align: center;
}
.headfooter{
  font-weight: 700;
  font-size: 1.42857143rem;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}
.secheadfooter{
  padding: 0 30px;
    margin-bottom: 50px;
}
p:last-child {
  margin-bottom: 30px;
}
.magic{
    width: 50px;
    height: 50px;
    position: absolute !important;
    right: 127px;
    bottom: -2px;
}
.dataField{
  text-align: center;
}
.spe{
  color: #000;
  text-decoration: underline;
}
/*home*/